import { ShapeFlags } from "@vue/shared";
import { createAppAPI } from "./apiCreateApp";
import { Text } from "./vnode";

export function createRenderer(options) {
  return baseCreateRenderer(options);
}

function baseCreateRenderer(renderOptions) {
  const hydrate = false; // 服务端渲染，不关心
  // 通用的DOM操作方法
  const {
    inset: hostInsert,
    remove: hostRemove,
    setElementText: hostSetElementText,
    setText: hostSetText,
    parentNode: hostParentNode,
    nextSibling: hostNextSibling,
    createElement: hostCreateElement,
    createText: hostCreateText,
    patchProp: hostPatchProp,
  } = renderOptions;

  // =======================
  // 渲染的核心流程
  // 通过闭包缓存内敛函数
  // =======================

  // 核心diff过程
  const patch = (n1, n2, container) => {
    // 核心的patch
    if (n1 === n2) return;

    const { type, shapeFlag } = n2;
    if (n1 === null) {
      //初始渲染  还有组件的初次渲染
      switch (type) {
        case Text:
          processText(n1, n2, container);
          break;
        default:
          if (shapeFlag & ShapeFlags.ELEMENT) {
            mountElement(n2, container);
          }
      }
    } else {
      // 更新流程
    }
  };
  // 挂载子节点
  function mountChildren(children, container) {
    for (let i = 0; i < children.length; i++) {
      patch(null, children, container);
    }
  }

  const processElement = () => {}; // 处理element

  // 挂载element
  function mountElement(vnode, container) {
    let { type, props, children, shapeFlag } = vnode;
    let el = (vnode.el = hostCreateElement(type));
    if (props) {
      for (let key in props) {
        hostPatchProp(el, key, null, props[key]);
      }
    }
    if (shapeFlag & ShapeFlags.TEXT_CHILDREN) {
      //孩子节点是文本
      hostSetElementText(children);
    } else if (shapeFlag & ShapeFlags.ARRAY_CHILDREN) {
      mountChildren(children, container);
    }
    hostInsert(el, container);
  }

  const processText = (n1, n2, container) => {
    if (n1 === null) {
      // 初始化
      hostInsert((n2.el = hostCreateText(n2.children)), container);
    }
  };

  const processFragment = () => {}; // 处理fragment节点

  const processComponent = () => {}; // 处理组件

  const mountComponent = () => {}; // 挂载组件

  const setupRenderEffect = () => {}; // 运行带副作用的render函数

  const render = (vnode, container) => {
    //渲染过程

    // 如果vnode是空，卸载逻辑
    if (vnode === null) {
      // 卸载逻辑
      if (container._vnode) {
        // 卸载组件
        // unmount();
      }
    } else {
      // 这里既有初始化也有更新逻辑`
      patch(container._vnode || null, vnode, container);
    }
    container._vnode = vnode;
  };

  // =======================
  // 🐶2000+行的内敛函数🐶
  // =======================

  return {
    render,
    hydrate, // 服务端渲染相关
    createApp: createAppAPI(render, hydrate),
  };
}
